.button {
  background-color: #FF651B;
  border: none;
  color: #FFFFFF;
  text-align: center;
  font-size: 21px;
  padding: 10px 30px 10px 10px;
  width: 200px;
  transition: all 0.5s;
  cursor: pointer;
  margin-top: 5px;
  border-radius: 20px 50px 50px 20px;
}

.button span {
  cursor: pointer;
  display: inline-block;
  position: relative;
  transition: 0.5s;
  content: '\00bb';
}

.button span:after {
  content: '\00bb';
  transition-duration: 300ms;
  position: absolute;
  opacity: 1;
  top: 0;
  right: -20px;
  
}

.button:hover span {
  padding-right: 20px;
  
}

.button:hover span:after {
  opacity: 1;
  right: 0;
  -webkit-transform: rotateZ(00deg); /* Safari */
transform: rotateZ(90deg); /* Standard syntax */
  display: inline-block;
}

